import React, { useState } from "react";

import AddToCartBtnBg from "../../../assets/icons/AddToCartBtnBg";

import styles from "./ProductInfo.module.sass";

const ProdictInfo = ({ name, price, desc, details }) => {
  const [selectedSize, setSelectedSize] = useState("XSS");

  const handleSizeChange = (e) => {
    setSelectedSize(e.target.value);
  };
  return (
    <div className={styles.productInfo}>
      <h1 className={styles.name}>{name}</h1>
      <p className={styles.price}>{price} &#8381;</p>
      <div className={styles.size}>
        <p className={styles.chooseSize}> :</p>
        <form action="" className={styles.sizeOptions}>
          <label htmlFor="XSS">
            <input
              type="radio"
              id="XSS"
              name="size"
              value="XSS"
              checked={selectedSize === "XSS"}
              onChange={handleSizeChange}
            />
            <span>XS S</span>
          </label>
          <label htmlFor="SM">
            <input
              type="radio"
              id="SM"
              name="size"
              value="SM"
              checked={selectedSize === "SM"}
              onChange={handleSizeChange}
            />
            <span>S M</span>
          </label>
          <label htmlFor="LXL">
            <input
              type="radio"
              id="LXL"
              name="size"
              value="LXL"
              checked={selectedSize === "LXL"}
              onChange={handleSizeChange}
            />
            <span>L XL</span>
          </label>
          <label htmlFor="ML">
            <input
              type="radio"
              id="ML"
              name="size"
              value="ML"
              checked={selectedSize === "ML"}
              onChange={handleSizeChange}
            />
            <span>M L</span>
          </label>
        </form>
      </div>
      <div className={styles.svgButton}>
        <AddToCartBtnBg />
        <span className={styles.buttonText}> </span>
      </div>
      <p className={styles.description}>{desc}</p>
      <div className={styles.dottedLine}></div>
      <p className={styles.madeOf}>
        <b>: </b>
        {details}
      </p>
    </div>
  );
};

export default ProdictInfo;
