import { useState, useEffect } from "react";
import { Link } from "react-router-dom";

import CloseBtn from "../../assets/icons/CloseBtn";
import Arrow from "../../assets/icons/Arrow";

import styles from "./index.module.sass";

const MobileFilter = () => {
  const [selectedPrice, setSelectedPrice] = useState("allprices");
  const [selectedSize, setSelectedSize] = useState("XSS");
  const [categories, setCategories] = useState([]);
  const [subcategories, setSubcategories] = useState([]);

  const handlePriceChange = (e) => {
    setSelectedPrice(e.target.value);
  };

  const handleSizeChange = (e) => {
    setSelectedSize(e.target.value);
  };

  useEffect(() => {
    fetch("https://640ef1d54ed25579dc40e2a6.mockapi.io/menu")
      .then((res) => res.json())
      .then((data) => setCategories(data));
  }, []);

  const handleGetArrOfSubcategories = (id) => {
    fetch(`https://640ef1d54ed25579dc40e2a6.mockapi.io/categories/a${id}`)
      .then((res) => res.json())
      .then((data) => setSubcategories(data.categories));
  };

  return (
    <div className={styles.mobileFilter}>
      <div className={styles.closeBtn}>
        <CloseBtn />
      </div>
      <div className={styles.content}>
        <div className={styles.categoriesWrapper}>
          <div className={styles.column}>
            <div className={styles.title}>:</div>
            <ul className={styles.categories}>
              {categories.map(({ id, name, categories }) => (
                <li key={id} className={styles.category}>
                  <Link to={`/catalog?menuId=${id}`} className={styles.link}>
                    {name}
                  </Link>
                  <span
                    className={styles.arrow}
                    onClick={() => handleGetArrOfSubcategories(id)}
                  >
                    <Arrow />
                  </span>
                </li>
              ))}
              {subcategories.length > 0 && (
                <ul className={styles.subcategories}>
                  {subcategories.map(({ id, name }) => (
                    <li key={id} className={styles.subcategory}>
                      <Link
                        to={`catalog?categoryId=${id}`}
                        className={styles.subcategoryLink}
                        onClick={() => setSubcategories(id)}
                      >
                        {name}
                      </Link>
                    </li>
                  ))}
                </ul>
              )}
            </ul>
          </div>
        </div>

        <div className={styles.column}>
          <div className={styles.title}>:</div>
          <form action="" className={styles.options}>
            <div className={styles.option}>
              <input
                type="radio"
                id="allprices"
                name="price"
                value="allprices"
                checked={selectedPrice === "allprices"}
                onChange={handlePriceChange}
              />
              <label htmlFor="allprices"> </label>
            </div>
            <div className={styles.option}>
              <input
                type="radio"
                id="upto500"
                name="price"
                value="upto500"
                checked={selectedPrice === "upto500"}
                onChange={handlePriceChange}
              />
              <label htmlFor="upto500"> 500</label>
            </div>
            <div className={styles.option}>
              <input
                type="radio"
                id="from500to1000"
                name="price"
                value="from500to1000"
                checked={selectedPrice === "from500to1000"}
                onChange={handlePriceChange}
              />
              <label htmlFor="from500to1000">500  1000</label>
            </div>
            <div className={styles.option}>
              <input
                type="radio"
                id="from1000to1500"
                name="price"
                value="from1000to1500"
                checked={selectedPrice === "from1000to1500"}
                onChange={handlePriceChange}
              />
              <label htmlFor="from1000to1500">1000  1500</label>
            </div>
            <div className={styles.option}>
              <input
                type="radio"
                id="from1500"
                name="price"
                value="from1500"
                checked={selectedPrice === "from1500"}
                onChange={handlePriceChange}
              />
              <label htmlFor="from1500"> 1500</label>
            </div>
          </form>
        </div>
        <div className={styles.column}>
          <div className={styles.title}>:</div>
          <form action="" className={styles.options}>
            <div className={styles.option}>
              <input
                type="radio"
                id="XSS"
                name="size"
                value="XSS"
                checked={selectedSize === "XSS"}
                onChange={handleSizeChange}
              />
              <label htmlFor="XSS">XS  S</label>
            </div>
            <div className={styles.option}>
              <input
                type="radio"
                id="SM"
                name="size"
                value="SM"
                checked={selectedSize === "SM"}
                onChange={handleSizeChange}
              />
              <label htmlFor="SM">S  M</label>
            </div>
            <div className={styles.option}>
              <input
                type="radio"
                id="ML"
                name="size"
                value="ML"
                checked={selectedSize === "ML"}
                onChange={handleSizeChange}
              />
              <label htmlFor="ML">M  L</label>
            </div>
            <div className={styles.option}>
              <input
                type="radio"
                id="LXL"
                name="size"
                value="LXL"
                checked={selectedSize === "LXL"}
                onChange={handleSizeChange}
              />
              <label htmlFor="LXL">L  XL</label>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default MobileFilter;
